import { auth } from "../../firebase/firebase";
import { LOCATION_ENABLE_SUCCESS,GET_INTEREST_IN_SUCCESS,UPDATE_INTEREST_IN_SUCCESS,GET_CONNECTION_SUCCESS,GET_ALL_INTEREST,UPDATE_ALL_INTEREST,GET_AUTH_PROFILE_DATA_SUCCESS, UPDATE_AUTH_PROFILE_SUCCESS, NOTIFICATION_ENABLE_SUCCESS, UPDATE_CONNECTION_SUCCESS, UPDATE_LOCATION_ENABLE_SUCCESS} from "../actions/types";

const Initial_state ={
    interest_in_Data : null,
    interest_in_UpdateData : null,
    get_connection_typeData:null,
    get_interest_data:null,
    update_interest_data:null,
    get_auth_profile_data:null,
    update_auth_profile_data:null,
    location_data:null,
    update_location_data:null,
    notification:null
   
}

export default (state=Initial_state,action) =>{
    const {type,payload} = action;
    switch (type){
        case GET_INTEREST_IN_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    interest_in_Data : payload?.data
                }
            };
        case UPDATE_INTEREST_IN_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    interest_in_UpdateData : payload?.data
                }
            };
        case GET_CONNECTION_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    get_connection_typeData : payload?.data
                }
            };
        case UPDATE_CONNECTION_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    update_connection_typeData : payload?.data
                }
            };
        case GET_ALL_INTEREST:
            if(payload.status){
                return{ 
                    ...state,
                    get_interest_data : payload?.data
                }
            };
        case UPDATE_ALL_INTEREST:
            if(payload.status){
                return{ 
                    ...state,
                    update_interest_data : payload?.data
                }
            };
        case GET_AUTH_PROFILE_DATA_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    get_auth_profile_data : payload?.data
                }
            };
        case UPDATE_AUTH_PROFILE_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    update_auth_profile_data : payload?.data
                }
            };
        case LOCATION_ENABLE_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    location_data : payload?.data
                }
            };
        case UPDATE_LOCATION_ENABLE_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    update_location_data : payload?.data
                }
            };
        case NOTIFICATION_ENABLE_SUCCESS:
            if(payload.status){
                return{ 
                    ...state,
                    notification : payload?.data
                }
            };
       
        default:
            return state;
    }
}