export const getFcmToken = async (setTokenFound) => {
    let currentToken = '';
    try {
        currentToken = await getToken(messaging, { vapidKey: publicKey });;
        if (currentToken) {
            setTokenFound(true);
        } else {
            setTokenFound(false);
        }
    } catch (error) {
        console.log('An error occurred while retrieving token ::::: ', error);
    }
    return currentToken;
};