import { GET_HOME_CONNECTIONS, HOME_LEFT_SWIPE, HOME_RIGHT_SWIPE, MY_PROFILE_SUCCESS, UNDO_SWIPE_SUCCESS } from "../actions/types";

const Initial_state = {
    home_data: null,
    left_swipe_data:null,
    right_swipe_data:null,
    my_profile_data:null,
    undo_swipe_data:null

}

export default (state = Initial_state, action) => {
    const { type, payload } = action;
    switch (type) {
        case GET_HOME_CONNECTIONS:
            if (payload.status) {
                return {
                    ...state,
                    home_data: payload?.data
                }
            };
        case HOME_LEFT_SWIPE:
            if (payload.status) {
                return {
                    ...state,
                    left_swipe_data: payload?.data
                }
            };
        case UNDO_SWIPE_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    undo_swipe_data: payload?.data
                }
            };
        case HOME_RIGHT_SWIPE:
            if (payload.status) {
                return {
                    ...state,
                    left_swipe_data: payload?.data
                }
            };
        case MY_PROFILE_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    my_profile_data: payload?.data
                }
            };

        default:
            return state;
    }
}