import { DELETE_CONNECTION_SUCCESS, DELETE_SENT_CONNECTION_REQUEST_SUCCESS, GET_CONNECTION_LIST_SUCCESS, GET_CONNECTION_RECIVIED_REQUEST_SUCCESS, GET_CONNECTION_SENT_REQUEST_SUCCESS } from "../actions/types";

const Initial_state ={
    sent_connection_req_data :null,
    recieve_connection_req_data :null,
    dele_sent_connection_req_data :null,
    connection_list_data:null,
    del_connection_data:null
}

export default (state = Initial_state, action) => {
    const { type, payload } = action;
    switch (type) {
        case GET_CONNECTION_SENT_REQUEST_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    sent_connection_req_data: payload?.data
                }
            }
        case GET_CONNECTION_RECIVIED_REQUEST_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    recieve_connection_req_data: payload?.data
                }
            }
        case DELETE_SENT_CONNECTION_REQUEST_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    dele_sent_connection_req_data: payload?.data
                }
            }
        case GET_CONNECTION_LIST_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    connection_list_data: payload?.data
                }
            }
        case DELETE_CONNECTION_SUCCESS:
            if (payload.status) {
                return {
                    ...state,
                    del_connection_data: payload?.data
                }
            }

        default:
            return state;
    }

}