import { auth } from "../../firebase/firebase";
import { REGISTER_LOGIN_SUCCESS, VERIFY_OTP_SUCCESS } from "../actions/types";

const Initial_state ={
    register_data : null,
    otp_verify_data : null,
}

export default (state=Initial_state,action) =>{
    const {type,payload} = action;
    switch (type){
        case REGISTER_LOGIN_SUCCESS:
            if(payload.status){
                return{
                    ...state,
                    register_data : payload?.data
                }
            };
        case VERIFY_OTP_SUCCESS:
            if(payload.status){
                return{
                    ...state,
                    otp_verify_data : payload?.data
                }
            };
        default:
            return state;
    }
}